@media screen and (max-width: 374px) {
  .container {
    width: 320px;

    margin: 0 auto;
    padding: 0 15px;
  }
}
@media screen and (min-width: 375px) {
  .container {
    width: 375px;
    margin: 0 auto;
    padding: 0 15px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    margin: 0 auto;

    padding: 0 20px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    margin: 0 auto;
    padding: 0 24px;
  }
}

.container_tranding {
  padding-top: 70px;
  outline: 2px solid red;
